import * as _ from 'lodash';
import * as EventEmitter from 'eventemitter3';
import { State } from '../State/State';
import { Phase } from '../Phase/Phase';
import { IComponent } from './IComponent';
import { IComponentConfig } from './IComponentConfig';
import { IComponentPhase } from '../Phase/IComponentPhase';
import { IComponentState } from '../State/IComponentState';

/**
 * Abstract UI Component base implementation.
 */
export abstract class Component<S> implements IComponent<S> {
  /**
   * Component static configuration.
   */
  protected config: IComponentConfig;

  /**
   * Component state storage.
   */
  protected state: IComponentState<S>;

  /**
   * Component phase control.
   */
  protected phase: Phase;

  /**
   * Component event handler.
   * @see {@link https://nodejs.org/api/events.html#events_class_eventemitter | Node: EventEmitter}
   */
  protected events: EventEmitter;

  /**
   * @template S
   * @param {IComponentConfig} config - static configuration.
   * @param {S?} state - initial state.
   */
  public constructor(config: IComponentConfig, state?: S) {
    this.config = config;
    this.phase = new Phase();
    this.state = new State<S>(state);
    this.events = new EventEmitter();
  }

  public getConfig(): IComponentConfig {
    return _.cloneDeep(this.config);
  }

  public getState(path?: string): S | any {
    return _.cloneDeep(this.state.get(path));
  }

  public changeState(state: S, extend = true): IComponent<S> {
    if (!this.phase.destroyed) {
      this.state.set(state, extend);

      this.render();
    }

    return this;
  }

  public getPhase(): IComponentPhase {
    return _.cloneDeep(this.phase.get());
  }

  public render(): IComponent<S> {
    this.phase.rendered = true;

    return this;
  }

  public init(): IComponent<S> {
    this.phase.initialized = true;

    return this;
  }

  public destroy(): void {
    this.phase.destroyed = true;
    this.state.clear();
  }

  public on(eventName: string, listener: (data?: any) => void): IComponent<S> {
    this.events.addListener(eventName, listener);

    return this;
  }

  public off(eventName?: string, listener?: (data?: any) => void): IComponent<S> {
    if (!eventName) {
      this.events.removeAllListeners();
    } else if (!listener) {
      this.events.removeAllListeners(eventName);
    } else {
      this.events.removeListener(eventName, listener);
    }

    return this;
  }

  protected fire(eventName: string, data?: any): IComponent<S> {
    this.events.emit(eventName, data);

    return this;
  }
}



