import * as _ from 'lodash';
import { IComponentState } from './IComponentState';

/**
 * Component State Storage
 */
export class State<S> implements IComponentState<S> {
  private state: S;

  /**
   * @template S
   * @param {S?} [initialState=null] - initial state.
   */
  public constructor(initialState: S = null) {
    this.state = initialState;
  }

  public clear(): IComponentState<S> {
    this.state = null;

    return this;
  }

  public get(path?: string): S | any {
    return path ? _.get(this.state, path) : this.state;
  }

  public set(value: S | any, extend = true, path?: string): IComponentState<S> {
    if (path) {
      _.set(this.state, path,
        extend ? _.extend({}, _.get(this.state, path), value) : value);
    } else {
      if (extend) {
        this.state = _.extend({}, this.state, value) as S;
      } else {
        this.state = value;
      }
    }

    return this;
  }
}



